import React from 'react';
import { useForm } from "react-hook-form";
import { CustomInput, CustomInputFile, Select } from 'components';
import { arrayCity, arrayTime } from 'utils';
import { FormContainer, InputName, InputDescription, InputPrice, InputTime, InputImage } from './styled';
const Component = () => {
    const { register, handleSubmit } = useForm();

    const onSubmit = (data) => {
        alert(JSON.stringify(data));
    };
    return (
        <FormContainer onSubmit={handleSubmit(onSubmit)} >
            <InputName>
                <CustomInput
                    type={"string"}
                    placeholder={"Ej. Bocina Bluetooth"}
                    label="Nombre del producto"
                    {...register("name")}

                />
            </InputName>
            <InputDescription>
                <CustomInput
                // type={"string"}
                // placeholder={"Ej. Bocina Bluetooth "}
                // isRequired={true}
                // label={"Descripción del producto"}
                // {...register("description")}
                />
            </InputDescription>
            <InputPrice>
                <CustomInput
                    type={"number"}
                    isRequired={true}
                    label={"Precio por día"}
                />
            </InputPrice>
            <InputTime>
                <Select label={"¿En cuánto tiempo puedes entregar el producto?"}
                    options={arrayTime} />
            </InputTime>
            <InputTime>
                <Select label={"¿En dónde tienes el producto?"}
                    options={arrayCity} />
            </InputTime>
            <InputImage>
                <CustomInputFile />
            </InputImage>
            <input type="submit" />
        </FormContainer>
    );
}

export default Component;
export { Component as Form }